import { Vector3 } from "../../maths/Vector3";

/**
	 * <code>Ray</code> 
	 */
export class Ray {
	/***/
	origin: Vector3;
	/***/
	direction: Vector3;

	/**
	 *  <code>Ray</code> 
	 * @param	origin 
	 * @param	direction  
	 */
	constructor(origin: Vector3, direction: Vector3) {
		this.origin = origin;
		this.direction = direction;
	}

	/**
	 * 
	 * @param t 
	 * @param out 
	 */
	at(t: number, out: Vector3) {
		Vector3.scale(this.direction, t, out);
		Vector3.add(this.origin, out, out);
	}

}

