import { IClone } from "../../../../utils/IClone"

/**
 * <code>Burst</code> 
 */
export class Burst implements IClone {
	/** ,*/
	private _time: number;
	/** */
	private _minCount: number;
	/** */
	private _maxCount: number;

	/**
	 * ,
	 * @return ,
	 */
	get time(): number {
		return this._time;
	}

	/**
	 * 
	 * @return 
	 */
	get minCount(): number {
		return this._minCount;
	}

	/**
	 * 
	 * @return 
	 */
	get maxCount(): number {
		return this._maxCount;
	}

	/**
	 *  <code>Burst</code> 
	 * @param time ,
	 * @param minCount 
	 * @param time 
	 */
	constructor(time: number = 0, minCount: number = 0, maxCount: number = 0) {
		this._time = time;
		this._minCount = minCount;
		this._maxCount = maxCount;
	}

	/**
	 * 
	 * @param	destObject 
	 */
	cloneTo(destObject: any): void {
		var destBurst: Burst = (<Burst>destObject);
		destBurst._time = this._time;
		destBurst._minCount = this._minCount;
		destBurst._maxCount = this._maxCount;
	}

	/**
	 * 
	 * @return	 
	 */
	clone(): any {
		var destBurst: Burst = new Burst(this._time, this._minCount, this._maxCount);
		this.cloneTo(destBurst);
		return destBurst;
	}
}

